@use 'variables' as v;
@use 'base/mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    min-height: 100%;
    min-width: 350px;
    display: flex;
    flex-direction: column;
    background-color: v.$crema;
    color: v.$morado;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    &.body-scroll{
        padding-top: 66.5px;
    }

}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}

.contenedor_small{
    width: 80%;
}
a {
    text-decoration: none;
}

img {
    max-width: 100%;
}
